import {
  EditOutlined,
  DeleteOutlined,
  AttachFileOutlined,
  GifBoxOutlined,
  ImageOutlined,
  MicOutlined,
  MoreHorizOutlined,
  VideocamOutlined,
} from "@mui/icons-material";
import {
  Box,
  Divider,
  Typography,
  InputBase,
  useTheme,
  Button,
  IconButton,
  useMediaQuery,
} from "@mui/material";
import FlexBetween from "components/FlexBetween";
import Dropzone from "react-dropzone";
import UserImage from "components/UserImage";
import WidgetWrapper from "components/WidgetWrapper";
import { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setPosts } from "state";

const MyPostWidget = ({ picturePath }) => {
  const dispatch = useDispatch();
  const [isImage, setIsImage] = useState(false);
  const [imageErrorMessage, setImageErrorMessage] = useState("");
  const [videoErrorMessage, setVideoErrorMessage] = useState("");
  const [image, setImage] = useState(null);
  //const [video, setVideo] = useState(null);
  //const [isVideoUploadVisible, setIsVideoUploadVisible] = useState(false);
  const [post, setPost] = useState("");
  const { palette } = useTheme();
  const { _id } = useSelector((state) => state.user);
  const token = useSelector((state) => state.token);

  const isNonMobileScreens = useMediaQuery("(min-width: 1000px)");
  const mediumMain = palette.neutral.mediumMain;
  const medium = palette.neutral.medium;

  const handlePost = async () => {
    const formData = new FormData();
    formData.append("userId", _id);
    formData.append("description", post);
    if (image) {
      formData.append("picture", image);
      formData.append("picturePath", image.name);
      /*if (video) {
        formData.append("video", video);
        formData.append("videoPath", video.name);
      }*/
    }
   

    const response = await fetch(`http://localhost:3001/posts`, {
      method: "POST",
      headers: { Authorization: `Bearer ${token}` },
      body: formData,
    });

    console.log(response)

    const posts = await response.json();
    dispatch(setPosts({ posts }));
    setImage(null);
    //setVideo(null);
    setPost("");
  };

  const handleDrop = (acceptedFiles) => {
    const file = acceptedFiles[0];
    const fileExtension = file.name.split(".").pop().toLowerCase();
    const allowedTypes = ["image/jpeg", "image/jpg", "image/png", "image/gif"];

    if (file && allowedTypes.includes(file.type)) {
      setImage(file);
      setImageErrorMessage("");
    } else {
      setImage(null);
      setImageErrorMessage("Invalid file type. Only JPEG, PNG, and GIF images are allowed.");
    }
  };

  /*const handleVideoDrop = (acceptedFiles) => {
    const file = acceptedFiles[0];
    const fileExtension = file.name.split(".").pop().toLowerCase();
    const allowedTypes = ["video/mp4", "video/mov"];

    if (file && allowedTypes.includes(file.type)) {
      setVideo(file);
      setVideoErrorMessage("");
    } else {
      setVideo(null);
      setVideoErrorMessage("Invalid file type. Only MP4 and MOV videos are allowed.");
    }
  };*/

  return (
    <WidgetWrapper>
      <FlexBetween gap="1.5rem">
        <UserImage image={picturePath} />
        <InputBase
  placeholder="Don't just talk about it. Be about it..."
  onChange={(e) => setPost(e.target.value)}
  value={post}
  multiline  // Enable multiline input
  rows={4}    // Set the number of visible rows (adjust as needed)
  inputProps={{ maxLength: 10000 }}  // Set character limit to 187
  sx={{
    width: "100%",
    backgroundColor: palette.neutral.light,
    borderRadius: "2rem",
    padding: "1rem 2rem",
    whiteSpace: "pre-wrap",  // Allow text wrapping
    wordWrap: "break-word",  // Break words when necessary
  }}
/>
{/* Character limit message */}
<Typography variant="caption" color="text.secondary" sx={{ textAlign: "right" }}>
        {post.length}/187 kharacters
      </Typography>

      </FlexBetween>
      {isImage && (
        <Box
          border={`1px solid ${medium}`}
          borderRadius="5px"
          mt="1rem"
          p="1rem"
        >
          <Dropzone
            acceptedFiles=".jpg,.jpeg,.png, .gif"
            multiple={false}
            onDrop={handleDrop} // Replace the existing onDrop with this line
            >          
            {({ getRootProps, getInputProps }) => (
              <FlexBetween>
                <Box
                  {...getRootProps()}
                  border={`2px dashed ${palette.primary.main}`}
                  p="1rem"
                  width="100%"
                  sx={{ "&:hover": { color:"#8a0303",cursor: "pointer" } }}
                >
                  <input {...getInputProps()} />
                  {!image ? (
                    <p className="upload-img-text">Only IMAGES and GIFs will be accepted for the time being.</p>
                  ) : (
                    <FlexBetween>
                      <Typography>{image.name}</Typography>
                      <EditOutlined />
                    </FlexBetween>
                  )}
                </Box>
                {image && (
                  <IconButton
                    onClick={() => setImage(null)}
                    sx={{ width: "15%" }}
                  >
                    <DeleteOutlined />
                  </IconButton>
                )}
              </FlexBetween>
            )}
          </Dropzone>
        </Box>
      )}

{/*{isVideoUploadVisible && (
        <Box border={`1px solid ${medium}`} borderRadius="5px" mt="1rem" p="1rem">
          <Dropzone
            acceptedFiles=".mp4,.mov"
            multiple={false}
            //onDrop={handleVideoDrop}
          >
            {({ getRootProps, getInputProps }) => (
              <FlexBetween>
                <Box
                  {...getRootProps()}
                  border={`2px dashed ${palette.primary.main}`}
                  p="1rem"
                  width="100%"
                  sx={{ "&:hover": { color: "#8a0303", cursor: "pointer" } }}
                >
                  <input {...getInputProps()} />
                  {!video ? (
                    <p className="upload-img-text">Only MP4 and MOV videos will be accepted for the time being.</p>
                  ) : (
                    <FlexBetween>
                      <Typography>{video.name}</Typography>
                      <EditOutlined />
                    </FlexBetween>
                  )}
                  </Box>*/}
                {/*{video && (
                  <IconButton /*onClick={() => setVideo(null)} sx={{ width: "15%" }}>
                    <DeleteOutlined />
                </IconButton>
                )}
              </FlexBetween>
            )}
          </Dropzone>
        </Box>
                )}*/}

     {/*} {videoErrorMessage && (
        <Typography variant="caption" color="error" sx={{ marginTop: "0.5rem", marginLeft:"8rem"}}>
          {videoErrorMessage}
        </Typography>
     )}*/}


      <Divider sx={{ margin: "1.25rem 0" }} />

      <FlexBetween>
        <FlexBetween gap="0.25rem" onClick={() => setIsImage(!isImage)}>
          <ImageOutlined sx={{ color: "green" }} />
          <Typography
            color={mediumMain}
            sx={{ "&:hover": { cursor: "pointer", color: "red" } }}
          >
            Image
          </Typography>
          {!isNonMobileScreens && (
            <FlexBetween gap="0.25rem">
             {/* <VideocamOutlined sx={{ color: "red" }} />
              <Typography sx={{ color:{mediumMain}, "&:hover": {cursor:"pointer",color:"green"} }}>Video</Typography>*/}
            </FlexBetween>
          )}
        </FlexBetween>

        {isNonMobileScreens ? (
          <>
            <FlexBetween gap="0.25rem" /*onClick={() => setIsVideoUploadVisible(!isVideoUploadVisible)}*/>
              <VideocamOutlined sx={{ color: "red" }} />
              <Typography color={mediumMain} sx={{ "&:hover": { cursor: "pointer", color: "green" } }}>Video</Typography>
            </FlexBetween>

            <FlexBetween gap="0.25rem">
              <AttachFileOutlined sx={{ color: mediumMain }} />
              <Typography color={mediumMain}>Attachment</Typography>
            </FlexBetween>

            <FlexBetween gap="0.25rem">
              <MicOutlined sx={{ color: mediumMain }} />
              <Typography color={mediumMain}>Chirp</Typography>
            </FlexBetween>
          </>
        ) : (
          <FlexBetween gap="0.25rem">
            <MoreHorizOutlined sx={{ color: mediumMain }} />
          </FlexBetween>
        )}
        <Button
          disabled={!post}
          onClick={handlePost}
          sx={{
            color: "skyblue",
            backgroundColor: "#8a0303",
            borderRadius: "3rem",
            "&:hover": { backgroundColor: "skyblue", color: "#8a0303" }
            
          }}
        >
          POST
        </Button>
      </FlexBetween>
    </WidgetWrapper>
  );
};

export default MyPostWidget;