import {
  ChatBubbleOutlineOutlined,
  FavoriteBorderOutlined,
  FavoriteOutlined,
  ShareOutlined,
  ThumbDown,
  ThumbDownOutlined,
  ThreeSixty,
  ThreeSixtyOutlined,
  DeleteOutlined,
} from "@mui/icons-material";
import { Box, Divider, IconButton, Typography, useTheme, Tooltip, TextField, Button } from "@mui/material";
import FlexBetween from "components/FlexBetween";
import Friend from "components/Friend";
import WidgetWrapper from "components/WidgetWrapper";
import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { setPost, setPosts } from "state";
import { format } from "date-fns";
import '../.././index.css'


const PostWidget = ({
  postId,
  postUserId,
  name,
  username,
  description,
  location,
  picturePath,
  videoPath,
  userPicturePath,
  likes,
  hates,
  boomerangs,
  comments,
  formattedTimestamp, // Updated prop name
  
  
}) => {
  const [isComments, setIsComments] = useState(false);
  const dispatch = useDispatch();
  const token = useSelector((state) => state.token);
  const loggedInUserId = useSelector((state) => state.user._id);
  const isLiked = Boolean(likes[loggedInUserId]);
  const likeCount = Object.keys(likes).length;
  const isHated = Boolean(hates[loggedInUserId]);
  const hateCount = Object.keys(hates).length;
  const isBoomeranged = Boolean(boomerangs[loggedInUserId]);
  const boomerangCount = Object.keys(boomerangs).length;
  //console.log(formattedTimestamp); // Add this line

  const commentCount = Object.keys(comments).length;
  const [commentContent, setCommentContent] = useState("");

  // Check if the logged-in user is the owner of the post
  const isOwnPost = postUserId === loggedInUserId;

 

  const handleDeletePost = async () => {
    try {
      const response = await fetch(`http://localhost:3001/posts/${postId}/delete`, {
        method: "PATCH",
        headers: {
          Authorization: `Bearer ${token}`,
        },
      });
  
      if (response.ok) {
        // Delete post from UI or trigger a refresh of the posts
        // e.g., dispatch an action to update the posts state
        // dispatch(deletePost(postId));
        console.log("Post deleted successfully.");
      } else {
        console.log("Failed to delete the post.");
      }
    } catch (error) {
      console.error("Error deleting the post:", error);
    }
  };

  const { palette } = useTheme();
  const main = palette.neutral.main;
  const primary = palette.primary.main;


  const patchLike = async () => {
    const response = await fetch(`http://localhost:3001/posts/${postId}/like`, {
      method: "PATCH",
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ userId: loggedInUserId }),
    });
    const updatedPost = await response.json();
    dispatch(setPost({ post: updatedPost }));
  };

  const patchHate = async () => {
    const response = await fetch(`http://localhost:3001/posts/${postId}/hate`, {
      method: "PATCH",
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ userId: loggedInUserId }),
    });
    const updatedPost = await response.json();
    dispatch(setPost({ post: updatedPost }));
  };


  /*const patchBoomerang = async () => {
    const response = await fetch(`http://localhost:3001/posts/${postId}/boomerang`, {
      method: "POST",
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ userId: loggedInUserId }),
    });
    const updatedPost = await response.json();
    dispatch(setPost({ post: updatedPost }));

    // Check if the post was boomeranged successfully
    if (updatedPost.boomerangs[loggedInUserId]) {
      // Make a request to the backend to create a new post with the original post data
      await fetch("http://localhost:3001/posts", {
        method: "POST",
        headers: {
          Authorization: `Bearer ${token}`,
          "Content-Type": "application/json",
        },
        body: JSON.stringify({ userId: loggedInUserId, originalPost: postId }),
      });

      // Fetch the updated list of posts from the backend
      const response = await fetch("http://localhost:3001/posts");
      const updatedPosts = await response.json();

      // Update the state with the modified posts array
      dispatch(setPosts(updatedPosts));
    }
  };*/
  



//BOOMERANG JUST INCASE I NEED TO COME BACK TO THIS!
  const patchBoomerang = async () => {
    const response = await fetch(`http://localhost:3001/posts/${postId}/boomerang`, {
      method: "POST",
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ userId: loggedInUserId }),
    });
    const updatedPost = await response.json();
    dispatch(setPost({ post: updatedPost }));
  };




  const patchComment = async () => {
    if (commentContent.trim() === "") {
      // Comment is empty, handle the error or show a message to the user
      return;
    }

    const newComment = {
      userId: loggedInUserId,
      username: {username}, // Replace with the actual username
      commentText: commentContent,
      createdAt: new Date().toISOString(),
    };
  
    const response = await fetch(`http://localhost:3001/posts/${postId}/comment`, {
      method: "PATCH",
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify({
        userId: loggedInUserId,
        commentText: commentContent, // Change comments to commentText
      }),
    });
  
    const updatedPost = await response.json();
    dispatch(setPost({ post: updatedPost }));
    setCommentContent(""); // Reset the comment input field after posting
  };
  
  const handleCommentChange = (e) => {
    const inputValue = e.target.value;
    if (inputValue.length <= 187) {
      setCommentContent(inputValue);
    }
  };
  


  const handleCommentSubmit = (e) => {
    e.preventDefault();
    patchComment();
  };


  const patchDelete = async () => {
    const response = await fetch(`http://localhost:3001/posts/${postId}/delete`, {
      method: "PATCH",
      headers: {
        Authorization: `Bearer ${token}`,
        "Content-Type": "application/json",
      },
      body: JSON.stringify({ userId: loggedInUserId }),
    });
    const updatedPost = await response.json();
    dispatch(setPost({ post: updatedPost }));
  };

  /*const handleCommentSubmit = () => {
    patchComment();
  };*/
  return (
    <Box className="post-widget-container" m="2rem 0">
    <WidgetWrapper m="2rem 0">
      
      
      <Friend
        friendId={postUserId}
        name={name}
        username= {username}
        subtitle={location}
        userPicturePath={userPicturePath}
      />
      
                     
     
        <Typography color={main} sx={{ mt: "1rem",
      width: "100%",
      padding: "1rem 2rem",
      whiteSpace: "pre-wrap",  // Allow text wrapping
      wordWrap: "break-word",  // Break words when necessary
        
        
      }}>
        {description}
      </Typography>
    
      

      {picturePath && (
        <img
          width="100%"
          height="auto"
          alt="post"
          style={{ borderRadius: "0.75rem", marginTop: "0.75rem" }}
          src={`http://localhost:3001/assets/${picturePath}`}
        />
      )}

{videoPath && (
          <video
            width="100%"
            height="auto"
            style={{ borderRadius: "0.75rem", marginTop: "0.75rem" }}
            controls
          >
            <source src={`http://localhost:3001/assets/${videoPath}`} type="video/mp4" />
            Your browser does not support the video tag.
          </video>
        )}


       {/* Display the formatted timestamp */}
       <Typography color="red" sx={{ mt: "0.5rem", textAlign: "center" }}>
  {formattedTimestamp && <span>{formattedTimestamp}</span>}
</Typography>
      <FlexBetween mt="0.25rem">
        <FlexBetween gap="1rem">
          <FlexBetween gap="0.3rem">
          <Tooltip title="Love">
            <IconButton onClick={patchLike}>
              {isLiked ? (
                <FavoriteOutlined sx={{ color: "red", "&:hover": { color:"#8a0303",cursor: "pointer" }}} />
              ) : (
                <FavoriteBorderOutlined />
              )}
            </IconButton>
            </Tooltip>
            <Typography>{likeCount}</Typography>
          </FlexBetween>
  
          <FlexBetween gap="0.3rem">
          <Tooltip title="Hate">
            <IconButton onClick={patchHate}>
              {isHated ? (
                <ThumbDownOutlined sx={{ color: "brown",  "&:hover": { color:"#77971b",cursor: "pointer" } }} />
              ) : (
                <ThumbDown />
              )}
            </IconButton>
            </Tooltip>
            <Typography>{hateCount}</Typography>
          </FlexBetween>

          <FlexBetween gap="0.3rem">
          <Tooltip title="Boomerang">
            <IconButton onClick={patchBoomerang}>
              {isBoomeranged ? (
                <ThreeSixtyOutlined sx={{ color: primary, "&:hover": {color: "#0275F4"} }} />
              ) : (
                <ThreeSixty />
              )}
            </IconButton>
            </Tooltip>
            <Typography>{boomerangCount}</Typography>
          </FlexBetween>
  
          <FlexBetween gap="0.3rem">
          <Tooltip title="Speak Your Peace">
            <IconButton onClick={() => setIsComments(!isComments)}>
              <ChatBubbleOutlineOutlined />
            </IconButton>
            </Tooltip>
            <Typography>{commentCount}</Typography>
          </FlexBetween>
        </FlexBetween>

 
        {/*<Tooltip title="Share">
        <IconButton>
          <ShareOutlined />
        </IconButton>
              </Tooltip>*/}
              </FlexBetween>
      {isComments && (
        <Box mt="0.5rem">
          {comments.map((comment, i) => (
  <Box key={`${name}-${i}`}>
    <Divider />
    <FlexBetween>
    <Typography sx={{ m: "0.5rem 0", pl: "1rem", maxWidth: "100%", wordWrap: "break-word" }}>
    <strong>{`@${username}`}</strong> - {comment.commentText}
        </Typography>
        </FlexBetween>
  </Box>
))}
          <Divider />
          <form onSubmit={handleCommentSubmit}>
          <TextField
        label="Leave a comment"
        variant="outlined"
        value={commentContent}
        onChange={handleCommentChange}
        fullWidth
        margin="dense"
        multiline
        maxRows={4}
        inputProps={{ maxLength: 187 }}
        size="small"
      />
            <Button variant="contained" color="primary" type="submit"
            sx={{"&:hover": { color:"#8a0303", backgroundColor: "skyblue" }}}
            >
              Post Comment
            </Button>
          </form>
        </Box>
      )}
    </WidgetWrapper>
    </Box>
  );
};

export default PostWidget;